<template>
  <div class="page-container">
    <div class="page-box">
      <div class="page-box-title">
        <h2>Admin - File upload test</h2>
      </div>
      <div class="page-box-content">
        <button class="button is-danger" @click="getClientsList">
          Get clients list
        </button>
        <button class="button is-danger" @click="getProjectsList">
          Get projects list
        </button>
        <button class="button is-danger" @click="getRoomsList">
          Get rooms list
        </button>
        <button class="button is-danger" @click="getMeasurementsList">
          Get measurements list
        </button>
        <div>
          <div v-if="clientsResult">
            <h2>Clients</h2>
            <table class="bulkresult-table">
              <tr>
                <th>trashed</th>
                <th>id</th>
                <th>Klantnaam</th>
                <th>Sales eigenaar</th>
                <th>Aangemaakt op</th>
              </tr>
              <tr v-for="item in clientsResult" :key="item.id">
                <td>{{ item.data().trashed }}</td>
                <td>{{ item.id }}</td>
                <td>{{ item.data().clientName }}</td>
                <td>{{ item.data().salesOwner }}</td>
                <td>{{ item.data().createdAt.seconds | secondsToDate }}</td>
              </tr>
            </table>
          </div>
          <div v-if="projectsResult">
            <h2>Projects</h2>
            <table class="bulkresult-table">
              <tr>
                <th>trashed</th>
                <th>id</th>
                <th>Projectnaam</th>
                <th>Stad</th>
                <th>Straat</th>
                <th>Postcode</th>
                <th>Huisnummer</th>
                <th>Bijbehorende klant</th>
                <th>Aangemaakt op</th>
              </tr>
              <tr v-for="item in projectsResult" :key="item.id">
                <td>{{ item.data().trashed }}</td>
                <td>{{ item.id }}</td>
                <td>{{ item.data().projectName }}</td>
                <td>{{ item.data().address.city }}</td>
                <td>{{ item.data().address.street }}</td>
                <td>{{ item.data().address.postCode }}</td>
                <td>{{ item.data().address.houseNumber }}</td>
                <td>{{ item.data().belongsTo }}</td>
                <td>{{ item.data().createdAt.seconds | secondsToDate }}</td>
              </tr>
            </table>
          </div>
          <div v-if="roomsResult">
            <h2>Rooms</h2>
            <table class="bulkresult-table">
              <tr>
                <th>trashed</th>
                <th>id</th>
                <th>Ruimtenaam</th>
                <th>Hoogte</th>
                <th>Breedte</th>
                <th>Lengte</th>
                <th>Interieurtype</th>
                <th>Extra wensen</th>
                <th>Correctiefactor</th>
                <th>Type</th>
                <th>Bijbehorend project</th>
                <th>Aangemaakt op</th>
              </tr>
              <tr v-for="item in roomsResult" :key="item.id">
                <td>{{ item.data().trashed }}</td>
                <td>{{ item.id }}</td>
                <td>{{ item.data().roomName }}</td>
                <td>{{ item.data().height }}</td>
                <td>{{ item.data().width }}</td>
                <td>{{ item.data().length }}</td>
                <td>{{ item.data().interior }}</td>
                <td>{{ item.data().wishes }}</td>
                <td>{{ item.data().correction }}</td>
                <td>{{ item.data().type }}</td>
                <td>{{ item.data().belongsTo }}</td>
                <td>{{ item.data().createdAt.seconds | secondsToDate }}</td>
              </tr>
            </table>
          </div>
          <div v-if="roomsResult">
            <h2>Scenarios</h2>
            <table class="bulkresult-table">
              <tr>
                <th>Index</th>
                <th>Actief</th>
                <th>Label</th>
                <th>Aantal producten</th>
                <th>Mensen aanwezig</th>
                <th>Aantal mensen</th>
                <th>Type mensen</th>
                <th>Bijbehorende ruimte</th>
              </tr>
              <tbody v-for="item in roomsResult" :key="item.id">
                <tr
                  v-for="(scenario, index) in item.data().scenarios"
                  :key="index"
                >
                  <td>{{ index }}</td>
                  <td>{{ scenario.active }}</td>
                  <td>{{ scenario.label }}</td>
                  <td>{{ scenario.addedProducts.length }}</td>
                  <td>{{ scenario.hasPeople }}</td>
                  <td>{{ scenario.peopleAmount }}</td>
                  <td>{{ scenario.peopleType }}</td>
                  <td>{{ item.id }}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div v-if="roomsResult">
            <h2>Products</h2>
            <table class="bulkresult-table">
              <tr>
                <th>Index</th>
                <th>Aantal</th>
                <th>Productnaam</th>
                <th>Oppervlakte</th>
                <th>Hoogte</th>
                <th>Breedte</th>
                <th>Diameter</th>
                <th>Afstand</th>
                <th>Bijbehorend scenario</th>
              </tr>
              <template v-for="item in roomsResult">
                <template v-for="(scenario, index) in item.data().scenarios">
                  <tr
                    v-for="(product, index2) in scenario.addedProducts"
                    :key="`${item.id}${index}${index2}`"
                  >
                    <td>{{ index2 }}</td>
                    <td>{{ product.amount }}</td>
                    <td>{{ product.productName }}</td>
                    <td>{{ product.area }}</td>
                    <td>{{ product.height }}</td>
                    <td>{{ product.width }}</td>
                    <td>{{ product.diameter }}</td>
                    <td>{{ product.distanceLabel }}</td>
                    <td>{{ item.id }}|{{ index }}</td>
                  </tr>
                </template>
              </template>
            </table>
          </div>
          <div v-if="measurementsResult">
            <h2>Measurements</h2>
            <table class="bulkresult-table">
              <tr>
                <th>trashed</th>
                <th>id</th>
                <th>Type</th>
                <th>hz125</th>
                <th>hz250</th>
                <th>hz500</th>
                <th>hz1000</th>
                <th>hz2000</th>
                <th>hz4000</th>
                <th>Uitgevoerd door</th>
                <th>Datum</th>
                <th>Beschrijving</th>
                <th>Bijbehorende ruimte</th>
                <th>Aangemaakt op</th>
              </tr>
              <tr v-for="item in measurementsResult" :key="item.id">
                <td>{{ item.data().trashed }}</td>
                <td>{{ item.id }}</td>
                <td>{{ item.data().type }}</td>
                <td>{{ item.data().hz125 }}</td>
                <td>{{ item.data().hz250 }}</td>
                <td>{{ item.data().hz500 }}</td>
                <td>{{ item.data().hz1000 }}</td>
                <td>{{ item.data().hz2000 }}</td>
                <td>{{ item.data().hz4000 }}</td>
                <td>{{ item.data().author }}</td>
                <td>
                  {{ item.data().date.seconds | secondsToDate }}
                </td>
                <td>{{ item.data().description }}</td>
                <td>{{ item.data().belongsTo }}</td>
                <td>{{ item.data().createdAt.seconds | secondsToDate }}</td>
              </tr>
            </table>
          </div>
        </div>
      </div>
      <div class="page-box-footer"></div>
    </div>
  </div>
</template>

<script>
import Firebase from "firebase";

export default {
  name: "Admin",
  data() {
    return {
      clientsResult: null,
      projectsResult: null,
      roomsResult: null,
      measurementsResult: null,
    };
  },
  computed: {
    roomsThatHaveScenarios() {
      if (this.roomsResult) {
        return this.roomsResult.filter((room) => {
          return room.data().scenarios;
        });
      } else {
        return null;
      }
    },
  },
  methods: {
    getClientsList() {
      Firebase.firestore()
        .collection("clients")
        .get()
        .then((querySnapshot) => {
          const clientsList = [];
          querySnapshot.forEach((doc) => {
            clientsList.push(doc);
          });
          this.clientsResult = clientsList;
        });
    },
    getProjectsList() {
      Firebase.firestore()
        .collection("projects")
        .get()
        .then((querySnapshot) => {
          const projectsList = [];
          querySnapshot.forEach((doc) => {
            projectsList.push(doc);
          });
          this.projectsResult = projectsList;
        });
    },
    getRoomsList() {
      Firebase.firestore()
        .collection("rooms")
        .get()
        .then((querySnapshot) => {
          const roomsList = [];
          querySnapshot.forEach((doc) => {
            roomsList.push(doc);
          });
          this.roomsResult = roomsList;
        });
    },
    getMeasurementsList() {
      Firebase.firestore()
        .collection("measurements")
        .get()
        .then((querySnapshot) => {
          const measurementsList = [];
          querySnapshot.forEach((doc) => {
            measurementsList.push(doc);
          });
          this.measurementsResult = measurementsList;
        });
    },
  },
  filters: {
    secondsToDate(seconds) {
      return new Date(seconds * 1000).toLocaleString("nl-NL", {
        day: "numeric",
        month: "numeric",
        year: "numeric",
      });
    },
  },
};
</script>

<style>
.bulkresult-table {
  width: 100%;
  font-size: 10px;
}
</style>


